import React from 'react';
import styles from './CustomerTypeSquare.module.scss';
import TicketIcon from 'gui/components/icons/TicketIcon';


interface ICustomerTypeSquareProps {
  icon?: any;
  iconStyle?: any;
  label?: string;
  children?: any;
  active?: boolean;
  onClick?: () => void,
}


const CustomerTypeSquare = (props: ICustomerTypeSquareProps) => (<div className={styles.customerTypeSquare}>
  <div className={`${styles.iconRow} ${props.active ? '' : styles.inactive}`.trim()}
       onClick={props.onClick}
  >
    <props.icon className={styles.icon} style={props.iconStyle}/>
  </div>
  <div className={styles.labelRow}>
    {props.label}
    {props.children}
  </div>
</div>);

CustomerTypeSquare.defaultProps = {
  icon: TicketIcon,
  label: '',
  iconStyle: {},
  active: true,
  onClick: () => console.log('CustomerTypeSquare click not implemented'),
};

export default CustomerTypeSquare;
