import React from 'react';
import styles from './Faq.module.scss';
import {IAuthenticatedPageProps} from 'gui/components/authenticatedPage/AuthenticatedPage';
import ScrollToTopExtended from 'gui/components/scrollToTopExtended/ScrollToTopExtended';
import Accordion from '@material-ui/core/Accordion';
import MuiAccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import {withStyles} from '@material-ui/core/styles';
import styled from "styled-components";
import AddRoundedIcon from '@material-ui/icons/AddRounded';
import RemoveRoundedIcon from '@material-ui/icons/RemoveRounded';
import {MDBCol, MDBRow} from 'mdbreact';
import HtmlTags from 'react-html-tag-attributes';
import CustomerTypeSquare from './components/customerTypeSquare/CustomerTypeSquare';
import TicketIcon from 'gui/components/icons/TicketIcon';
import ShoppingCartIcon from 'gui/components/icons/ShoppingCartIcon';
import CrispChatContainer from 'data/redux/containers/crispChatContainer';


interface IFaqProps extends IAuthenticatedPageProps {

}

const Icon = styled(props => (
  <div {...props}>
    <div className="y"><AddRoundedIcon/></div>
    <div className="n"><RemoveRoundedIcon/></div>
  </div>
))`
  & > .y {
    display: block;
  }

  & > .n {
    display: none;
  }

  .Mui-expanded & > .n {
    display: block;
  }

  .Mui-expanded & > .y {
    display: none;
  }
`;

const AccordionSummary = withStyles({
  // ... other styles
  expandIcon: {
    transform: 'none !important',
  },
})(MuiAccordionSummary);

enum CUSTOMER_TYPES {
  SELLER,
  BUYER
}


const questionsAndAnswers = [
  {
    title: "האם ניתן ליצור קשר עם המוכר על מנת להוריד את מחיר הכרטיס?",
    content: "לא ניתן ליצור קשר ישיר עם המוכר המקורי. מכירת הכרטיס או כל מוצר אחר באתר מתבצעת ישירות מולנו דיל אנד גו. המחיר נקבע מבלי יכולת של הרוכש לשנותו.",
    type: CUSTOMER_TYPES.BUYER,
  },
  {
    title: "האם אפשר למצוא מחיר זול יותר באתרים אחרים?",
    content: "מחירי הכרטיסים / שוברים באתר דיל אנד גו הינם אטרקטיביים מאוד אך יחד עם זאת, באחריות הרוכש לבדוק כי המחיר מתאים לו ותחרותי ביחס למקומות אחרים.",
    type: CUSTOMER_TYPES.BUYER,
  },
  {
    title: "האם יש הבדל בין כרטיס/שובר אלקטרוני לכרטיס/שובר פיזי?",
    content: "באתר דיל אנד גו ניתן להוריד את הכרטיסים לאחר שהועברו על שימך באיזור האישי. הכרטיס מצורף כקובץ וניתן להשתמש בו ככרטיס פיזי.",
    type: CUSTOMER_TYPES.BUYER,
  },
  {
    title: "מה קורה אם שמי אינו מאויית נכון בכרטיס?",
    content: "בעת רכישת הכרטיס הינך נדרש למלא את הפרטים המלאים של האנשים אליהם מועברים הכרטיסים תוך הקפדה על איות נכון. במידה והפרטים מולאו כמו שצריך וחלה טעות בתהליך השינוי, נבצע את השינויים הנדרשים כדי שיתאים לפרטים שהועברו אך במידה והעברת הפרטים נעשתה ע\"י הרוכש  באופן לא תקין, באחריות הרוכש לבצע את השינוי.",
    type: CUSTOMER_TYPES.BUYER,
  },
  {
    title: "האם ניתן לרכוש כרטיסים באתר זה כפי שניתן לרכוש בכל סוכנות נסיעות אחרת?",
    content: "הכרטיסים המוצעים באתר זה הם כרטיסים שנמכרים על ידי לקוחות שנאלצו לבטל טיסתם או שאינם יכולים להגיע לאירוע שתכננו. התאריכים, המקומות ופרטי הכרטיס הם לרב ספציפיים ולא ניתנים לשינוי. מכיוון שכך, מחירי הכרטיסים יהיו לרב נמוכים משמעותית מאלה שניתן למצוא אצל סוכני הנסיעות.",
    type: CUSTOMER_TYPES.BUYER,
  },
  {
    title: "רכשתי שובר בסכום של 1000 דולר. האם ניתן לפצל אותו?",
    content: "ברוב המקרים, ניתן לפצל את השימוש בשובר אך חשוב לוודא ולבדוק עפ\"י ההערות המופיעות על השובר עצמו.",
    type: CUSTOMER_TYPES.SELLER,
  },
  {
    title: "איך אני משלם עבור הכרטיסים / שובר?",
    content: "התשלום מתבצע באמצעות כרטיס אשראי כאשר העסקה מחויבת לאחר שהכרטיסים / השוברים הועברו לבעלות הרוכש.",
    type: CUSTOMER_TYPES.BUYER,
  },
  {
    title: "למה שארצה למכור את הכרטיס שברשותי באתר די אנד גו?",
    content: "ראשית, אם יש לך את הזמן לכך, נמליץ לך לפרסם את הכרטיס בכמה שיותר מקומות על מנת להגיע לכמה שיותר אנשים. באתר שלנו אין צורך להיפגש עם האנשים בצד השני, אין את ההתמקחות לאחר קביעת המחיר על ידך. הכל נעשה בצורה דיגיטלית כולל העברת הכרטיס לצד השני.",
    type: CUSTOMER_TYPES.SELLER,
  },
  {
    title: "האם כמוכר, אני יכול לשנות את מחיר הכרטיס/שובר?",
    content: "המוכר קובע את מחיר המכירה הרצוי ויכול לשנות את המחיר בכל רגע נתן כל עוד הכרטיסים / שוברים לא נמכרו / הוצע הצעה עבורם.",
    type: CUSTOMER_TYPES.SELLER,
  },
  {
    title: "איך אני יודע מתיי נמצא רוכש לכרטיס שהעמדתי למכירה?",
    content: "ניתן להתעדכן בכל עת באיזור האישי. בנוסף, אתה מקבל מיל והודעת SMS בכל פעם שנעשה שינוי בסטטוס המודעה שלך לרבות מה של הצעה חדשה",
    type: CUSTOMER_TYPES.SELLER,
  },
  {
    title: "איך אני מעביר את השם שלי מהכרטיס לשמו של הרוכש?",
    content: "בעת ההצעה המוגשת לך עבור הכרטיס יצויינו שמות הלקוחות והפרטים הנדרשים על מנת לבצע שינוי זה. מחובתך לבצע את השינוי מול חברת התעופה/ ספק הנסיעות/ ספק האירוע גם אם נדרש תשלום כלשהו עבור שינוי זה(כולל תשלום דמי שינוי שם במידה ונדרש).",
    type: CUSTOMER_TYPES.SELLER,
  },
  {
    title: "יש ברשותי ארבעה כרטיסי טיסה של כל בני משפחתי. האם ניתן למכור רק אחד?",
    content: "באירוע שבו מספר כרטיסים, ניתן למכור את הכרטיסים כיחידים או כקבוצה את כולם.",
    type: CUSTOMER_TYPES.SELLER,
  },
  {
    title: "איך אקבל תשלום עבור כרטיס שנמכר?",
    content: "התשלום מתבצע ישירות מחברת דיל אנד גו כ 10 ימים לאחר אישור העסקה מצדך והעברת הבעלות על הכרטיס. התשלום יבוצע בהעברה בנקאית ישירות לחשבונך.",
    type: CUSTOMER_TYPES.SELLER,
  },


];

class Faq extends React.PureComponent<IFaqProps, any> {
  constructor(props) {
    super(props);
    this.state = {
      [CUSTOMER_TYPES.SELLER]: true,
      [CUSTOMER_TYPES.BUYER]: true
    };
  }

  render() {
    return (
      <section className={`container-xl ${styles.FaqContainer}`}>
        <HtmlTags itemtype="https://schema.org/FAQPage"/>
        <CrispChatContainer/>
        <ScrollToTopExtended bottom={95} scrollOnRender smooth/>
        <MDBRow>
          <MDBCol sm="8" md="9" lg="10">
            <h2 style={{
              fontWeight: 700,
              fontSize: 40,
            }}> שאלות ותשובות</h2>
          </MDBCol>
          <MDBCol sm="4" md="3" lg="2"/>
        </MDBRow>
        <MDBRow>
          <MDBCol>
            <CustomerTypeSquare icon={ShoppingCartIcon}
                                iconStyle={{marginTop: 14}}
                                active={this.state[CUSTOMER_TYPES.SELLER]}
                                onClick={() => this.setState({[CUSTOMER_TYPES.SELLER]: !this.state[CUSTOMER_TYPES.SELLER]})}
            >
              מוכר
            </CustomerTypeSquare>
            <CustomerTypeSquare icon={TicketIcon}
                                active={this.state[CUSTOMER_TYPES.BUYER]}
                                onClick={() => this.setState({[CUSTOMER_TYPES.BUYER]: !this.state[CUSTOMER_TYPES.BUYER]})}
            >
              קונה
            </CustomerTypeSquare>
          </MDBCol>
        </MDBRow>
        <MDBRow style={{
          marginTop: 30,
          marginBottom: 30,
        }}>
          <MDBCol>
            {questionsAndAnswers
              .filter(({type}) => this.state[type] === true)
              .map(({title, content, type}) =>
                <Accordion itemScope itemProp="mainEntity" itemType="https://schema.org/Question" key={title + content}
                           id={`questionType-${type}`}>
                  <AccordionSummary
                    expandIcon={<Icon/>}
                    aria-controls="panel1a-content"
                  >
                    <Typography itemProp="name">{title}</Typography>
                  </AccordionSummary>
                  <AccordionDetails itemScope itemProp="acceptedAnswer" itemType="https://schema.org/Answer">
                    <Typography itemProp="text">
                      {content}
                    </Typography>
                  </AccordionDetails>
                </Accordion>)}
          </MDBCol>
        </MDBRow>
      </section>

    );
  }
}

export default Faq;
